import React, { Component } from 'react';
import { get, getTourAuthKey, isNullOrEmpty } from 'utils';
import { connect } from 'react-redux';
import * as action from 'action';
import $ from 'jquery';

const uri = 'tour/detailImage';

class TourDetailGallery extends Component {
  constructor(props){
    super(props);

    if (this.props.history != null) {
      //this.props.setHistory(this.props.history);
    }

    this.state = {
      photoUrl: '',
      idx: 0,
    }

    this.photoList = [];
  }
  
  componentDidMount() {
    window.setSwipeCallBack(
      (dist) => {
        this.props.toLeft(dist);
      },
      (dist) => {
        this.props.toRight(dist);
      },
      'dv-tour-detail-gallery');
    this.requestData();
  }

  requestData(params) {
    const { id, type } = this.props.match.params;
    
    var data = {
      ServiceKey: getTourAuthKey(),
      contentTypeId: type,
      contentId: id,
      MobileOS: window.os, 
      MobileApp: window.appName,
      imageYN: 'Y', 
      _type: 'json',
    }

    get(uri, data, 
      (res) => {
        console.log(res);
        var list = res.response.body.items.item;
        for (var i = 0; i < list.length; ++i) {
          list[i].originimgurl = list[i].originimgurl.replace('http://', '//');
          list[i].smallimageurl = list[i].originimgurl.replace('http://', '//');
          list[i].idx = i;
        }
        console.log(list);
        this.setState({
          ...this.state,
          data: list,
          photoUrl : list[0].originimgurl,
          idx: 0,
        });
      },
      (res) => {
        console.log(res);
      });
  }

  renderPhoto(idx) {
    if (idx != this.state.idx && idx > -1 && idx < this.state.data.length) {
      var url = this.state.data[idx].originimgurl;
      //console.log(url);
      this.setState({
        ...this.state,
        photoUrl: url,
        idx: idx,
      });
  
      var listObj = $('#dv-tour-detail-gallery-list')[0];
      var scrollLeft = listObj.scrollLeft;
      var scrollWidth = listObj.scrollWidth;
      var itemWidth = scrollWidth / this.state.data.length;
      var width = listObj.clientWidth;
      var animate = true;
      var offset = 20;
      // 오른쪽 영역 밖 이미지 클릭 시.
      var val = undefined;
      if ((itemWidth * (idx + 1)) + offset > width + scrollLeft) {
        val = (itemWidth * (idx + 1)) - width + offset;
        //listObj.scrollLeft = scrollLeft - val;
      }
      // 왼쪽 영역 밖 이미지 클릭 시.
      else if (itemWidth * idx - offset < scrollLeft) {
        val = itemWidth * idx - offset;
        //listObj.scrollLeft = itemWidth * idx;
      }
      if (val) {
        if (animate) {
          $('#dv-tour-detail-gallery-list').animate({scrollLeft: val}, 200);
        }
        else {
          listObj.scrollLeft = val;
        }
      }
    }
  }

  renderItems() {
    var result = [];
    if (this.state.data) {
      var list = this.state.data;
      
      list.forEach(element => {
        var url = element.smallimageurl;
        var idx = element.idx;
        result.push(
          <div className={'item' + (idx == this.state.idx ? ' selected' : '') } onClick={ () => { this.renderPhoto(idx); }} >
            <img key={idx} src={url} />
          </div>);
      });
    }
    return (<div id='dv-tour-detail-gallery-list'>
        <div className='list'>
          {result}
        </div>
      </div>) ;
  }

  render(){
    return (
      <div className='inner-box' id='dv-tour-detail-gallery'>
        <div className='inner-body'>
          {this.renderItems()}
          <div id ='dv-tour-detail-gallery-image'>
            <img onClick={(obj) => {
              this.renderPhoto(this.state.idx + (obj.pageX < obj.target.width * 0.5 ? -1 : 1))
                //console.log(obj.pageX < obj.target.width * 0.5 ? 'left' : 'right');
                }
              } 
              src={this.state.photoUrl} />
          </div>
        </div>
      </div>);
  }
}


const mapStateToProps = (state) => {
  return {
      //user: state.auth.user
  };
}

const mapDispatchToProps = (dispatch) => {
  return {
    setHistory: (history) => { dispatch(action.setHistory(history)) }
      //setPage: (page, header, footer) => { dispatch(action.setPage(page, header, footer)) }
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(TourDetailGallery);