import React, { Component } from 'react';
import { getUrl } from 'utils';
import ReactPlayer from 'react-player';
import { connect } from 'react-redux';
import * as action from 'action';

class Upload extends Component {
  constructor(props){
    super(props);

    if (this.props.history != null) {
      this.props.setHistory(this.props.history);
    }

    this.state = {
      selectedFile: null,
      path: '',
    };
  }

  onChangeHandler(event) {
    console.log(event.target.files[0]);
    this.setState({
      selectedFile: event.target.files[0],
      loaded: 0,
      path: URL.createObjectURL(event.target.files[0]),
    });
  }

  handleSubmit(event) {
    event.preventDefault();

    const formData = new FormData();
    const { selectedFile } = this.state;

    formData.append('inputFile', selectedFile);

    fetch(getUrl('file/movie/upload'), {
      method: 'POST',
      body: formData,
    });
  }

  render(){
    return (
      <div style={{position: 'relative', left: '5%', width: '90%'}}>
        <br/><br/>
        <form onSubmit={ (event) => { this.handleSubmit(event); }}>
          <label>
            Upload a file: <br/><br/>
            <input type='file' name='file' onChange={ (event) => { this.onChangeHandler(event); }}/>
          </label>
          <br/><br/>
          <button type='submit'>Upload</button>
        </form >
        <ReactPlayer
          className='react-player'
          width='90%' height='100%'
          url={this.state.path} playing={false} controls={true}>
        </ReactPlayer>
        <div>재생</div>
        <br/><br/><br/><br/>
      </div >
    );
  }
}


const mapStateToProps = (state) => {
  return {
      //user: state.auth.user
  };
}

const mapDispatchToProps = (dispatch) => {
  return {
    setHistory: (history) => { dispatch(action.setHistory(history)) }
    //setPage: (page, header, footer) => { dispatch(action.setPage(page, header, footer)) }
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(Upload);